

.chart {
    width: 100%;
    height: 40%;
    display: flex;
    justify-content: space-evenly;
}

.chart-bar-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 5rem 1rem 0.5rem 0;

}
.chart-bar {
    position: relative;
}

.chart-bar-title {
    margin: 0;
    padding: 0;
    color: var(--clr-medium-brown);
}

.inner{
    width: 100%;
    bottom: 0;
    position: absolute;
    transition: height 500ms;
    display: flex;
    justify-content: center;

}
.inner:hover{
    cursor: pointer;
}


.tool-tip {
    padding: 0.5rem 0.7rem;
    width: 100%;
    text-align: center;
    background-color: var(--clr-dark-brown);
    color: var(--clr-white);
    border-radius: 10px;
    position: absolute;
    top: -3rem;
}